.start {
  background-color: #dadbd3;
}

.app {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: #dadbd3;
  background-image: url(https://www.infoskaters.com/images/whatsapp/whatsapp_marketing_banner.jpg);
  
  background-repeat: no-repeat;
  background-size: 100% 350px ;
}

.app_body {
  display: flex;
  background-color: #ededed;
  margin-top:-30px;
  height: 90vh;
  width: 90vw;
  box-shadow: -1px 4px 20px -6px rgba(0,0,0,0.75);
}
