.sidebar {
    display:flex;
    flex-direction: column;
    flex:0.35;
}  

.sidebar__header {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgray;
}

.sidebar__header__right {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}

.sidebar__header__right > .MuiSvgIcon-root {
    margin-right: 2vw;
    font-size: 24px !important;
}

.sidebar__search {
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 40px;
    padding: 10px;
}
.sidebar__search__container {
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;;
    height: 35px;
    border-radius: 20px;
}

.sidebar__search__container > .MuiSvgIcon-root{
    margin-left: 6px;
    color:grey;
    padding-block: 10px;
}

.sidebar__search__container > input {
    border:none;
    margin-left: 10px;

    /* removes the box  */
    outline-width: 0;
}

.sidebar__chat__component {
    flex:1;
    background-color: white;
    overflow-y:auto;
}